.card {
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	width: 370px;
	border-top: 1px solid var(--grayE4);
	padding: 10px 0;
}
.pomodoros {
	display: inline-block;
	box-sizing: content-box;
	width: 23px;
	height: 19px;
	padding-top: 4px;
	margin-right: 10px;
	border: 1px solid var(--grayC4);
	border-radius: 50%;
	text-align: center;
	line-height: 17px;
	font-weight: 300;
	font-size: 16px;
}

.menu {
	position: relative;
	width: 165px;
	border: 1px solid var(--grayF4);
	padding: 5px 0;
	background: var(--grayF4);
	z-index: 2;
}

.menu-item {
	display: flex;
	align-items: center;
	width: 100%;
	padding: 8px 5px 8px 15px;
	font-size: 16px;
}

*:disabled {
	cursor: default;
}

*:disabled > *, *:disabled > * > * {
	color: var(--grayC4);
	fill: var(--grayC4);
}

.menu-item:hover {
	background-color: var(--grayE4);
}

.menu-item :first-child {
	width: 15px;
}

.menu-item > span {
	margin-left: 9px;
}
/* Dropdown triangle */
.menu::before {
	content: ' ';
	position: absolute;
	width: 0;
	height: 0;
	top: -10px;
	left: 50%;
	margin-left: -10px;
	border-style: solid;
	border-width: 0 10px 10px 10px;
	border-color: transparent transparent var(--grayF4) transparent;
}

.edit-task-input {
	margin-right: 8px;
}
.edit-task-form {
	display: flex;
}

.task-info {
	display: flex;
	align-items: center;
}

.icon-btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 30px;
	height: 30px;
	padding: 7px;
	border-radius: 50%;
	border: 1px solid var(--black);
	margin-right: 10px;
}

.confirm-btn {
	border-color: var(--green);
}
